.popup-menu__container {
  align-items: center;
  background-color: rgba(0,0,0,.5);
  bottom: 0;
  display: flex;
  justify-content: center;
  right: 0;
  left: 0;
  position: fixed;
  top: 0;
  visibility: hidden;
  z-index: 1;
  width: 0;
  transition: 0.3s width;
}

.popup-menu__container_opened {
  visibility: visible;
  width: 100%;
}

/* The side navigation menu */
.popup-menu__sidenav {
  height: 100%; /* 100% Full-height */
  position: fixed; /* Stay in place */
  top: 0; /* Stay at the top */
  right: 0;
  width: 67%;
  background-color: #FAFAFA; /* background-color*/
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 160px; /* Place content 60px from the top */
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: .3s width;
}

/* The navigation menu links */
.popup-menu__navlink {
  margin-bottom: 28px;
  text-decoration: none;
  display: block;
  transition: .3s opacity;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
}

.popup-menu__navlink_account {
  box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.08);
  font-size: 13px;
  line-height: 12px;
  border-radius: 15px;
